.swiper-slide{
  height: 100vh;
}


.slide-1{
  background: url(../img/OH_MY_STILL_LIFE97929_fin_hor.jpg) no-repeat center bottom / cover;
  display: flex;
  align-items: center;
  justify-content: center;
  .flex{
    transform: translateX(60px);
  }
}

.logo{
  width: 200px;
  img{
    width: 100%;
  }
}

.logo-absolute{
  position: absolute;
  left: 50%;
  bottom: 28px;
  transform: translateX(-50%);
}

.flex{
  display: flex;
  justify-content: center;
  align-items: center;
}

.text{
  font-size: 35px;
  letter-spacing: 1px;
  color: #fff;
  text-transform: uppercase;
  margin-left: 80px;
}

.flex-left{
  width: 50%;
  height: 100vh;
}

.flex-right{
  width: 50%;
  overflow: hidden;
}

.description{
  font-size: 18px;
  font-weight: normal;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}

.title{
  font-weight: 500;
  font-size: 4vw;
  b{
    font-weight: bold;
  }
}

.line{
  display: inline-block;
  position: relative;
  &:after{
    content: "";
    position: absolute;
    left: -5px;
    height: 3px;
    width: calc(100% + 10px);
    background-color: #ceabd1;
    top: 2.3vw;
  }
}


.slide-2{
  .description{
    text-align: right;
    margin-bottom: 30px;
    padding-right: 35px;
  }

  .pic{
    transform: translateX(10vw);
    img{
      width: auto;
    }
  }

  .title{
    padding-left: 100px;
  }
}



.slide-3{
  .flex-left{
    display: flex;
    justify-content: flex-end;
    flex-wrap: wrap;
  }
  .flex-right{
    display: flex;
    justify-content: space-between;
  }
  .title{
    padding-left: 100px;
    padding-top: 70px;
    margin-bottom: 30px;
    width: 100%;
    height: 150px;
  }
  .pic{
    display: flex;
    align-items: flex-end;
    font-size: 4vw;
    justify-content: center;
    font-weight: 500;
    padding-bottom: 50px;
    letter-spacing: 4px;
  }
  .pic-1{
    height: 60vh;
    width: 500px;
    margin-right: 120px;
    position: relative;
    &:after{
      content: "";
      position: absolute;
      width: 20px;
      height: 100%;
      left: 100%;
      top: 0;
      margin-left: 50px;
      background-color: #fbf3a7;
    }
  }
  .pic-2{
    height: 75vh;
    width: calc(50% - 120px);
    position: relative;
    margin-top: 16vh;
    &:after{
      content: "";
      position: absolute;
      width: 20px;
      height: 100%;
      left: 100%;
      top: 0;
      margin-left: 50px;
      background-color: #ceabd1;
    }
  }
  .pic-3{
    width: 50%;
    height: 90vh;
    margin-top: 2vh;
  }
}

a{
  display: block;
}
.slide-4{
  background: url(../img/purple-line.svg) no-repeat right 80px bottom -10px / auto 150%;
  .flex-left{
    display: flex;
    align-items: flex-end;
  }
  .pic-2{
    max-width: 400px;
    display: flex;
    a{
      height: 100%;
    }
    img{
      max-height: 50vh;
      max-width: 100%;
    }
  }
  .flex-right{
    padding-right: 50px;
    padding-left: 50px;
  }
  .title{
    max-width: 800px;
    font-weight: normal;
    font-size: 3vw;
    b{
      font-weight: bold;
    }
  }
  .descrtiption{
    padding-left: 300px;
    margin-top: -80px;
    margin-bottom: 100px;
  }
}

.slide-5{
  .flex-right{
    display: flex;
    justify-content: center;
    img{
      width: 60%;
    }
  }
  .flex-left{
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    text-transform: uppercase;
    font-weight: 500;
  }
  .title{
    font-size: 7vw;
  }
}


.slide-6{
  background: url(../img/bg-6.png) no-repeat left / 50% 100%;
  padding-left: 50px;
  .flex-left{
    display: flex;
    align-items: center;
  }
  .title{
    font-size: 3vw;
  }
  .pic{
    position: absolute;
    left: 47%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 40vw;
    z-index: 100;
  }

  .pic-2{
    position: absolute;
    top: 0;
    right: 50px;
    width: 50vw;
    img{
      width: 100%;
    }
  }
  .pic-3{
    position: absolute;
    top: 0;
    right: 0;
    width: 40vw;
  }
  .flex-right{
    width: 50%;
    height: 100vh;
    position: relative;
    
  }

  .descriptions{
    position: absolute;
    right: 50px;
    bottom: 100px;
    
  }

  .description{
    &:first-child{
      transform: translateX(-100px);
    }
  }

  .plus{
    font-size: 80px;
    font-weight: bold;
    line-height: 80px;
    color: #ceabd1;
    margin-bottom: 20px;
  }
}

.slide-7{
  .flex-right{
    background-color: #d6e9fa;
    align-self: center;
    padding-bottom: 50px;
    border-right: 5vw solid #fff;
  }

  .pic{
    margin-top: -130px;
    margin-bottom: -160px;
  }

  .title{
    font-size: 2vw;
  }

  .title-right{
    display: flex;
    justify-content: flex-end;
    padding-right: 50px;
  }

  .description{
    text-align: right;
    padding-right: 50px;
    transform: translateY(80px);
  }
  .flex-left{
    display: flex;
    align-items: center;
    font-size: 7vw;
    font-weight: bold;
    padding-left: 5vw;
    color: #fff;
  }
}
